import React, { useState } from "react";
import { Upload, message } from "antd";
import { LoadingOutlined, PlusOutlined } from "@ant-design/icons";

const getBase64 = (img, cb) => {
  const reader = new FileReader();
  reader.onloadend = () => {
    cb(reader.result);
  };
  reader.readAsDataURL(img);
};

const beforeUpload = (file) => {
  const isJpgOrPng = file.type === "image/jpeg" || file.type === "image/png";
  if (!isJpgOrPng) {
    message.error("JPG/PNG íŒŒì¼ë§Œ ì—…ë¡œë“œ í•˜ì„¸ìš”.");
  }
  // const isLt2M = file.size / 1024 / 1024 < 2;
  // if(!isLt2M){
  //     message.error('ì´ë¯¸ì§€ ìš©ëŸ‰ì€ 2MB ì´í•˜ë§Œ ê°€ëŠ¥í•©ë‹ˆë‹¤. ')
  // }
  return isJpgOrPng;
};

export const Preview = (props) => {
  const [loading, setLoading] = useState(false);
  const { url, setUrl, setOpt } = props;

  const dummyRequest = ({ file, onSuccess }) => {
    setTimeout(() => {
      onSuccess("ok");
    }, 0);
  };

  const handleChange = (info) => {
    if (info.file.status === "uploading") {
      setLoading(true);
      return;
    }
    if (info.file.status === "done") {
      getBase64(info.file.originFileObj, (imgUrl) => {
        setUrl(imgUrl); // prop: setImgurl

        setOpt(true);
        setLoading(false);
      });
    }
  };
  const uploadBtn = (
    <div>
      {loading ? <LoadingOutlined /> : <PlusOutlined />}
      <div style={{ marginTop: 8, fontSize: "0.75em" }}>ë°°ê²½ ì´ë¯¸ì§€</div>
    </div>
  );

  return (
    <>
      <label htmlFor="canvas-image" style={{ display: "none" }}>
        ë°°ê²½ ì´ë¯¸ì§€
      </label>
      <Upload
        name="background-img"
        listType="picture-card"
        showUploadList={false}
        beforeUpload={beforeUpload}
        onChange={handleChange}
        customRequest={dummyRequest}
        className="preview-image"
      >
        {url ? (
          <img
            src={url}
            alt="preview"
            style={{ width: "100%", height: "100%" }}
          />
        ) : (
          uploadBtn
        )}
      </Upload>
    </>
  );
};
